.certificates-section {
    background-color: #d1d3d4;
    height: 100%;
    width: 100%;
}
.title{
    padding-top: 3cm;
    color: black;
    font-weight: bold;
    text-align: center;
    font-size: 20pt;
    font-family: Arial;
}
.section{
    margin: 0 0 0 8.6cm;
    width: 50%;
    height: 100%;
    display: block;
}
.cert-pic{
    width: 90%;
    height: 10cm;
    margin-left: 2rem;
    margin-bottom: 1.5rem;
}
.certificate-card{
    border-radius: 15px;
    margin-top: 3rem;
    background-color: white;
}
.cert-name{
    font-family: Arial;
    font-weight: bold;
    text-align: center;
    background-color: var(--s-color);
    padding-top: 0.6rem;
    height: 4rem;
}
.more{
    height: 4rem;
}
.more-button{
    background-color: var(--s-color);
    color:black;
    margin-left: 46%;
    width: 3cm;
    height: 2rem;
    border-radius: 15px;
    border: var(--secondary-color) solid 2px;
    margin-top: 1rem;
    font-size: 12pt;
    font-family: Arial;
    font-weight: bolder;
    cursor: pointer;
}
.more-button:hover{
    opacity: 0.8;
    transition: 0.2s;
}