/* src/styles/Skills.css */
.skills-section {
    padding: 2rem;
    background-color: var(--bg1-color);
    color: var(--t-color);
    text-align: center;
}

.skills-section h2 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
}

.skills-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
}

.skill-card {
    background-color: var(--bg2-color);
    border: 1px solid var(--t-color);
    border-radius: 8px;
    padding: 1rem;
    width: 150px;
    transition: transform 0.3s ease;
}

.skill-card:hover {
    transform: scale(1.05);
}

.skill-card h3 {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

.skill-card p {
    font-size: 1rem;
    color: var(--s-color);
}
.skill-card img{
    width: 4rem;
    height: 4rem;
}