.home-container{
    width: 100%;
    height: 100%;
}
.about{
    width: 100%;
    height: 666px;
    background-color: var(--secondary-color);
    display: flex;
}
.about-text{
    width: 50%;
    height: 100%;
    left:0;
}
.about-text h2{
    margin-top: 12rem;
    margin-left: 4rem;
    text-align: center;
}
.about-text p{
    text-align: center;
    margin: 1rem 0 0 3rem;
    font-family: Arial;
    font-size: 18px;
    font-weight: lighter;
    border: #010004 solid 1px;
    padding: 10px;
    border-radius: 10px;
}
.about-img{
    height: 100%;
    width: 50%;
    right: 0;
}
.about-img-top{
    border-radius: 20px;
    width: 12cm;
    height: 12cm;
    margin-top: 9rem;
    margin-left: 5rem;
}
.thanking-message{
    width: 100%;
    height: 4cm;
    background-color: white;
    text-align: center;
    padding: 1rem 4cm 0 4cm;
}
.thanking-message p a{
    text-decoration: none;
    color: var(--s-color);
}